<template>
  <el-container>
    <el-header>周周个人小站</el-header>
    <el-main>
      <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
        <el-form-item label="账号" prop="name">
          <el-input type="text" v-model="ruleForm2.name"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass">
          <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="phone">
          <el-input v-model.number="ruleForm2.phone"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm2')">注册</el-button>
          <el-button @click="resetForm('ruleForm2')">重置</el-button>
        </el-form-item>
      </el-form>
    </el-main>
  </el-container>
</template>
<script>
  export default {
    data() {
      var checkPhone = (rule, value, callback) => {
        var reg = /^1[34578]\d{9}$/i;
        if (!reg.test(value)) {
          return callback(new Error('手机号码格式不正确'));
        }
        callback();
      };
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      var name = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入账号'));
        } else {
          callback();
        }
      };
      return {
        ruleForm2: {
          pass: '',
          checkPass: '',
          phone: '',
          name: ''
        },
        rules2: {
          name: [
            { validator: name, trigger: 'blur' }
          ],
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          phone: [
            { validator: checkPhone, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        var self = this;
        self.$refs[formName].validate((valid) => {
          if (valid) {
            var data = '&name=' + self.ruleForm2.name + '&password=' + self.ruleForm2.checkPass + '&phone=' + self.ruleForm2.phone;
            self.$ajax.post('/api/Home/Index/add', data).then(function (response) {
                  self.$router.push({ path: `/mainContain` });        
              }).catch(function (error) {
              console.log(error);
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        var self = this;
        var id = '&id=' + 1;
        self.$ajax.post('/api/Home/Index/remove', id,
          ).then(function (response) {
              console.log(response);
        }).catch(function (error) {
          console.log(error);
        });
        this.$refs[formName].resetFields();
      }
    }
  }
</script>
<style scoped>
  .el-form {
    padding: 0 10vw 0 2vw;
    box-sizing: border-box;
  }

  .el-header {
    line-height: 60px;
  }
</style>


